a {
  color: currentColor;
  text-decoration: none;
}

.clock-data, .timer-title {
  margin: 0;
  padding: 0;
}

.color-timer-body {
  padding: 0;
}

.timer {
  min-height: 300px;
  background-color: #333;
  justify-content: center;
  align-items: center;
  display: flex;
}

.timer-container {
  text-align: center;
}

.timer-title {
  text-align: left;
  margin: 0px, 0px, 20px;
  color: #f33;
  margin-bottom: 15px;
  padding: 0;
  font-size: 40px;
}

.clock-data-container {
  justify-content: center;
  align-items: baseline;
  gap: 10px;
  margin-bottom: 20px;
  display: flex;
}

.clock-data {
  color: #ccc;
  padding: 0;
  font-size: 50px;
  transition: opacity 1s linear;
}

.clock-data--center {
  position: relative;
}

.clock-data--center:before {
  content: ":";
  font-size: 35px;
  position: absolute;
  bottom: -20%;
  left: -5px;
  transform: translate(-50%, -50%);
}

.clock-data--center:after {
  content: ":";
  font-size: 35px;
  position: absolute;
  bottom: -20%;
  left: 110%;
  transform: translate(-50%, -50%);
}

@keyframes blinker {
  50% {
    opacity: 0;
  }
}

.clock-data--animation {
  animation-name: blinker;
  animation-duration: 3s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.clock-data__ms {
  font-size: 20px;
}

.timer-button {
  width: 75px;
  height: 30px;
  background-color: #ccc;
  border: 4px solid #aaa;
  border-radius: 4px;
  transition: color .2s linear;
}

.timer-button:not(:last-child) {
  margin-right: 6px;
}

